import { FC } from "react";
import JSONInput from "react-json-editor-ajrm";

import { dark_vscode_tribute, localeEn } from "../../utils/jsonEditor";

type BaseJsonEditorProps = {
  height: string;
  onChange?: (e: any) => void;
  id?: string;
  placeholder?: any;
};

export const BaseJsonEditor: FC<BaseJsonEditorProps> = ({
  onChange,
  id,
  height,
  placeholder,
}) => {
  return (
    <div>
      <JSONInput
        id={id}
        colors={dark_vscode_tribute}
        locale={localeEn}
        height={height}
        width="100%"
        placeholder={placeholder}
        onChange={onChange}
      />
    </div>
  );
};
